.basket-card {
  display: grid;
  grid-template-columns: 100px 1fr;
  grid-template-rows: min-content auto;
  column-gap: 30px;

  @include vp-639 {
    grid-template-columns: 118px 1fr;
    column-gap: 15px;
    row-gap: 10px;
  }

  &__image-wrapper {
    grid-row: 1 / 3;

    width: 100px;
    height: 128px;

    @include vp-639 {
      grid-row: 2 / 3;

      width: 118px;
      height: 145px;
    }

    img {
      width: 100px;
      height: 128px;

      object-fit: cover;

      @include vp-639 {
        width: 118px;
        height: 145px;
      }
    }
  }

  &__title {
    margin: 0;

    font-weight: 400;
    font-size: 18px;
    line-height: normal;

    color: $color-nero;

    @include vp-639 {
      grid-column: 1 / 3;
      grid-row: 1 / 2;

      max-width: 213px;

      font-size: 15px;
      line-height: 20px;
    }
  }

  &__info {
    display: flex;

    @include vp-639 {
      flex-direction: column;
      margin-left: auto;
    }
  }

  &__parameters {
    display: flex;
    flex-direction: column;
    margin-right: auto;

    @include vp-639 {
      margin-right: 0;
      margin-bottom: 23px;
    }
  }

  &__size-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  &__label {
    display: block;
    margin-right: 10px;

    font-weight: 400;
    font-size: 15px;
    line-height: normal;

    color: $color-grey;

    @include vp-639 {
      font-size: 14px;
      line-height: 18px;
    }
  }

  &__size {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 24px;
    height: 18px;

    font-weight: 400;
    font-size: 12px;
    line-height: 18px;

    border: 1px solid $color-light-gray;
    border-radius: 2px;
    color: $color-nero;
  }

  &__color-wrapper {
    display: flex;
    align-items: center;
  }

  &__color {
    display: block;

    width: 20px;
    height: 20px;

    border-radius: 50%;

    &--white {
      border: 1px solid $color-silver;
    }
  }

  &__price {
    margin: 0 50px 0 0;

    font-weight: 400;
    font-size: 16px;
    line-height: 21px;

    color: $color-nero;

    @include vp-1439 {
      margin: 0 20px 0 0;
    }

    @include vp-1199 {
      margin: 0 50px 0 0;
    }

    @include vp-639 {
      margin: 0 0 10px;

      font-size: 14px;
      line-height: 18px;
    }
  }

  &__quantity {
    margin: 0 50px 0 0;

    font-weight: 400;
    font-size: 16px;
    line-height: 21px;

    color: $color-grey;

    @include vp-1439 {
      margin: 0 20px 0 0;
    }

    @include vp-1199 {
      margin: 0 50px 0 0;
    }

    @include vp-639 {
      margin: 0 0 10px;

      font-size: 14px;
      line-height: 18px;
    }
  }

  &__total-price {
    margin: 0;

    font-weight: 600;
    font-size: 16px;
    line-height: 21px;

    @include vp-639 {
      font-size: 14px;
      line-height: 18px;
    }
  }
}
